import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import './app.css'
import Panel01 from "./panels/Panel01";
import Panel02 from "./panels/Panel02";
import Panel03 from "./panels/Panel03";
import Panel04 from "./panels/Panel04";
import Panel05 from "./panels/Panel05";
import Panel06 from "./panels/Panel06";
import Panel07 from "./panels/Panel07";
import Panel08 from "./panels/Panel08";
import Panel09 from "./panels/Panel09";
import Panel10 from "./panels/Panel10";
import Result from "./panels/Result";
import ModalRoot from "@vkontakte/vkui/dist/components/ModalRoot/ModalRoot";
import ModalCard from "@vkontakte/vkui/dist/components/ModalCard/ModalCard";
import Avatar from "@vkontakte/vkui/dist/components/Avatar/Avatar";

//БОЕВЫЕ
//id группы с которой работаем
const group_id = "38225492";

//сервисный ключ приложения
const service_token = '04614c4e04614c4e04614c4e31040e81570046104614c4e5a2f2207a95ff71161a7cfc5'

//id админа
const user_id = "498675145"

//токен группы
const token = 'bd14672544fbf0b47a0ecdf2148fe31ed35eeb0ba17487a1b282861fbe0cc39dfb651569b7b447faf0a37'


const App = () => {
	const [activePanel, setActivePanel] = useState('01');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [resultArr, setResultArr] = useState([]);

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			switch (type) {
				case 'VKWebAppUpdateConfig':
					const schemeAttribute = document.createAttribute('scheme');
					schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
					document.body.attributes.setNamedItem(schemeAttribute);
					break;
				default:
					console.log(type);
			}

		});
		async function fetchData() {
			const user = await connect.send('VKWebAppGetUserInfo');
			setUser(user);
			addGroup()
			setPopout(null);
		}
		fetchData();

	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const setResult = (questionNumber, info, nextPage) => {
		setResultArr(resultArr.concat(info))
		setActivePanel(nextPage)
	}

	const addGroup = () => {
		connect.send("VKWebAppJoinGroup", {"group_id": +group_id});
	}


	const sendResult = () => {
		if (fetchedUser) {
			let wellDate = new Date();
			let sendInfo = `Пользователь: https://vk.com/id${fetchedUser.id}, имя: ${fetchedUser.first_name}. Ответил на вопросы  ${wellDate.toLocaleString("ru", {year: 'numeric', month: 'long', day: 'numeric', timezone: 'UTC', hour: 'numeric', minute: 'numeric', second: 'numeric'})}. Ответы: 
			1) ${resultArr[0]}, 2) ${resultArr[1]}, 3) ${resultArr[2]}, 4) ${resultArr[3]}, 5) ${resultArr[5]}, 6) ${resultArr[5]}, 7) ${resultArr[6]}, 8) ${resultArr[7]}, 9) ${resultArr[8]}, 10) ${resultArr[9]}, `
			let random_id = Math.floor(1000000000 + Math.random() * (9000000000 + 1 - 1000000000));
			connect.send("VKWebAppCallAPIMethod", {
				"method": "messages.send",
				"request_id": "sendOrder",
				"params": {
					"user_id": user_id,
					"v": "5.102",
					"random_id": random_id,
					"peer_id": group_id,
					"message": sendInfo,
					"access_token": token
				}
			});

		}
	}

	
	return (
		<View activePanel={activePanel} popout={popout}>
			<Panel01 id='01' setResult={setResult} />
			<Panel02 id='02' setResult={setResult} />
			<Panel03 id='03' setResult={setResult} />
			<Panel04 id='04' setResult={setResult} />
			<Panel05 id='05' setResult={setResult} />
			<Panel06 id='06' setResult={setResult} />
			<Panel07 id='07' setResult={setResult} />
			<Panel08 id='08' setResult={setResult} />
			<Panel09 id='09' setResult={setResult} />
			<Panel10 id='10' setResult={setResult} />
			<Result id='result' setResult={setResult} sendResult={sendResult}/>
		</View>
	);
}

export default App;

